@import url(https://fonts.googleapis.com/css?family=Lato:400,900);
h2 {
  font-size: 16px;
  font-weight: 400;
  margin: 7px; }

p {
  font-size: 12px; }

.color-green {
  color: #01a279; }

.color-blue {
  color: #005da3; }

button {
  cursor: pointer;
  outline: none;
  border: 1px solid #005da3;
  background: none;
  border-radius: 20px;
  color: #005da3;
  padding: 10px 30px;
  font-size: 12px; }

.button--blue-border {
  border-color: #005da3;
  color: #005da3; }

.button--blue-full {
  border-color: #005da3;
  color: white;
  background: #005da3; }

.button--green-border {
  border-color: #01a279;
  color: #01a279; }

.button--green-full {
  border-color: #01a279;
  color: white;
  background: #01a279; }

.button--red-border {
  border-color: #f65e50;
  color: #f65e50; }

.Modal {
  padding: 40px;
  position: absolute;
  top: 20%;
  left: 20%;
  right: 20%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background-color: white; }

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(41, 41, 45, 0.5); }

.modal-error {
  width: 180px;
  left: calc(50% - 90px); }

.center, .errorMsg {
  text-align: center; }

.center-2d {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.m-t-30 {
  margin-top: 30px; }

.errorMsg {
  color: #f65e50;
  margin-top: 30%; }

.children-card {
  cursor: pointer;
  box-shadow: 0px 0px 25px 0px rgba(162, 162, 162, 0.2);
  text-align: center;
  padding: 20px 0;
  margin: 40px; }
  .children-card__avatar {
    margin: auto;
    position: relative;
    height: 80px;
    width: 80px; }
    .children-card__avatar-photo {
      border-radius: 100%;
      height: 100%;
      width: auto; }
    .children-card__avatar-icon {
      position: absolute;
      top: -5px;
      right: -15px;
      height: 30px; }
  .children-card__text {
    padding-top: 10px; }
  .children-card__icon {
    width: 20px;
    height: auto; }

.checkOut-info {
  text-align: center;
  margin-bottom: 30px; }

.modal-out {
  width: 320px;
  left: calc(50% - 200px); }

.modal-in {
  width: 420px;
  left: calc(50% - 250px); }

.checkIn-selectBox {
  border: 1px solid #01a279;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 10px;
  margin: 10px auto;
  color: #01a279;
  border-radius: 30px; }
  .checkIn-selectBox select {
    background: white;
    border: none;
    color: #01a279; }

body {
  color: #3b384c;
  font-family: 'Lato', sans-serif; }

